@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@400;500;600&family=Roboto:wght@300;400;500;700&display=swap');

body, main, nav, section, article, footer, h1, h2, h3, h4, h5, h6, p, a, ul, li, dt, dd, address, figure, figcaption, form, fieldset, input, select, textarea {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 100%;
  text-decoration: none;
  font-weight: normal;
  color: inherit;
  box-sizing: border-box;
}

html {
    font-size: 62.5%;
}
#root {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, 'Roboto Slab', 'Roboto', 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* giving the main content container a gradient background */
  background: linear-gradient(to bottom, rgba(8, 76, 97, .35) 25vh, rgba(8, 76, 97, .5) 50vh, rgba(8, 76, 97, .8) 75vh, rgba(8, 76, 97, 1) 100vh) no-repeat;
  height: 100vh;
}

    /* setting sizing for one piece of the content on the main dashboard. adding more and fixing to come. */

    @media (min-width: 700px) {
      .dashboard-text {
        text-align: center;
      }
     
    }